import { Drawer } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../../helper/function";
import { ApiDocService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { Box } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import JsonViewerComponent from "../../../test/JsonViewerComponent";

export default function ResultExecApiComponent(props: any) {
    const [response, setResponse] = useState({});
    const [statusCode, setStatusCode] = useState();
    const [nameResponse, setNameResponse] = useState();
    const { setIsLoading } = props;

    function handleOnClose() {
        props.setVisible(false);
    }

    const loadData = useCallback(() => {
        if (props.visible === true) {
            setIsLoading(true);
            ApiDocService.RunApi(props.projectKey, props.folderKey, props.keyExec, (data: any) => {
                let row = data.data.data;
                try {
                    setResponse(JSON.parse(row.response));
                }
                catch (error) {
                    setResponse({ error: row.response })
                }
                setStatusCode(row.statusCode);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.visible, props.projectKey, props.folderKey, props.keyExec, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickSaveResponse() {
        props.setIsLoading(true);
        let body = {
            name: nameResponse,
            response: response
        }

        ApiDocService.SaveResponse(props.projectKey, props.folderKey, props.keyExec, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            open={props.visible}
            onClose={handleOnClose}
            size="large">
            <Box>
                <MyFormItem.Input
                    label="Name"
                    value={nameResponse}
                    setValue={setNameResponse} />

                <MyButton
                    value="Save response"
                    onClick={handleClickSaveResponse} />
                <p>Status code: {statusCode}</p>
                <p>Response:</p>
                <JsonViewerComponent data={response} />
            </Box>
        </Drawer>
    )
}